import Home from "@/layout";
import AuthRouter from "../utils/authRouter";
import lazyLoad from "../utils/lazyLoad";
import { lazy } from "react";
import { Navigate, RouteObject } from "react-router-dom";

// 首页模块
const homeRouter: RouteObject[] = [
	{
		path: '/home',
		element: <AuthRouter><Home /></AuthRouter>,
		children: [
			{
				index: true,
				element: <Navigate to='/home/app' />
			},
			{
				path: 'app',
				element: lazyLoad(lazy(() => import("../../views/app")))
			}, {
				path: 'web',
				element: lazyLoad(lazy(() => import("../../views/web"))),
				children: [
					{
						index: true,
						element: <Navigate to='/home/web/js/dashboard' />
					},
					{
						path: 'js/dashboard',
						element: lazyLoad(lazy(() => import("../../views/web/js/dashboard")))
					}, {
						path: 'js/sourcemap',
						element: lazyLoad(lazy(() => import("../../views/web/js/sourcemap")))
					}, {
						path: 'event',
						element: lazyLoad(lazy(() => import("../../views/web/event")))
					}, {
						path: 'setting',
						element: lazyLoad(lazy(() => import("../../views/web/setting/setting")))
					}, {
						path: 'access',
						element: lazyLoad(lazy(() => import("../../views/web/setting/access")))
					}]
			}, {
				path: 'system',
				element: lazyLoad(lazy(() => import("../../views/system"))),
				children: [
					{
						index: true,
						element: <Navigate to='/home/system/user' />
					}, {
						path: 'user',
						element: lazyLoad(lazy(() => import("../../views/system/user")))
					}, {
						path: 'role',
						element: lazyLoad(lazy(() => import("../../views/system/role")))
					}, {
						path: 'team',
						element: lazyLoad(lazy(() => import("../../views/system/team")))
					}]
			},]
	}
];

export default homeRouter;
